
import slide from 'components/slide/index'
import http from 'js/http'
import sel from 'components/sel/index'
import {limitFloating} from 'js/mixin'
import {mapGetters} from 'vuex'
import CheckIcon from 'vux/src/components/check-icon/index.vue'


export default {
name: "upgrade",
components: {
slide,
sel,
CheckIcon
},
data() {
return {
table: [
[
'升级节点', '微店节点', '动力节点', '中级节点', '高级节点'
],
[
'动力节点', '2000GRT', '', '', ''
],
[
'中级节点', '8000GRT', '6000GRT', '', ''
],
[
'高级节点', '20000GRT', '18000GRT', '12000GRT', ''
],
[
'超级节点', '50000GRT', '48000GRT', '42000GRT', '30000GRT'
],
],
form: {
type_id: '',
grt_address: '',
grt_num: '',
recommend_mobile: '',
remove_recommend: false
},
nodeSelData: [],
btnLoading: false,
recommend_name: '',
hasRecommend: true,
}
},
methods: {
submitFrom() {
if (this.myNodeInfo.isTenure) {
this.$vux.toast.show('任职状态不能升级')
return
}
if (this.form.type_id >= this.myNodeInfo.typeId) {
this.$vux.toast.show('只能升级到更高的节点')
return
}
if (this.form.type_id === '1' && this.hasRecommend && !this.form.remove_recommend) {
this.$vux.toast.show('请同意清除关系')
return
}
let grt = Number(this.form.grt_num)
if (!grt) {
this.$vux.toast.show('请输入有效的贵人通数量')
return
}
if (!this.form.grt_address) {
this.$vux.toast.show('请输入贵人通钱包地址')
return
}
this.upgradeNode()
},
upgradeNode() {
this.btnLoading = true
http.post('/node/upgrade', this.form, (res) => {
this.btnLoading = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.$vux.toast.show({
text: res.msg,
type: 'success'
})
setTimeout(() => {
this.$router.push({
path: '/personal'
})
}, 1500)
})
},
getRecommendMsg() {
if (!this.form.recommend_mobile) return
http.post('/node/recommend-mobile', {mobile: this.form.recommend_mobile}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.recommend_name = res.content.realname
})
},
getNodeSel() {
http.post('/node/type-list', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.nodeSelData = res.content
let nodeInfo = this.nodeSelData[0]
this.form.type_id = nodeInfo.id

})
},
changeNode(item) {
this.form.type_id = item.id

},
getHasRecommend() {
http.post('/node/has-recommend', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.hasRecommend = res.content
})
}
},
computed: {
...mapGetters([
'myNodeInfo'
]),
},
created() {
this.getNodeSel()
this.getHasRecommend()
},
watch: {
'form.grt_num'(v) {
let n = limitFloating(v)
this.form.grt_num = n
},
},
}



